import React, { useState, useEffect } from 'react';
import axios from 'axios';
import Pagination from '../components/BradDev/Pagination';
import Posts from '../components/BradDev/Posts';
import './bootstrap.min.css';

const Paginatez = () => {

    const [id] = useState('');
    const [posts, setPosts,] = useState([]);
    const [loading, setLoading] = useState(false);
    const [currentPage, setCurrentPage] = useState(1);
    const [postsPerPage] = useState(12);
  
    useEffect(() => {
      const fetchPosts = async () => {
        setLoading(true);
        const res = await axios.get('https://myprojectimg.firebaseio.com/myprojectimg.json');
        setPosts(res.data);
        setLoading(false);
      };
  
      fetchPosts();
    }, []);
  
    // Get current posts
    const indexOfLastPost = currentPage * postsPerPage;
    const indexOfFirstPost = indexOfLastPost - postsPerPage;
    const currentPosts = posts.slice(indexOfFirstPost, indexOfLastPost);
  
    // Change page
    const paginate = pageNumber => setCurrentPage(pageNumber);
  
    return (
      <div className='container mt-5'>
        <h1 className='text-primary mb-3'>My Blog</h1>
        <Posts key={id} posts={currentPosts} loading={loading} />
        <Pagination
          postsPerPage={postsPerPage}
          totalPosts={posts.length}
          paginate={paginate}
        />
      </div>
    );
  };
  
 export default Paginatez;




